body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    overflow-y: scroll;
}

#root {
    display: flex;
    justify-content: center;
    background-color: #DDDDDD;
}

.main {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    max-width: 1920px;
    width: 100%;
    background-color: #DDDDDD;
}

.content-wrap {
    display: flex;
    flex-direction: column;
}

.main h3 {
    margin: 10px 0 0 10px;
    border-color: transparent;
    border-bottom-color: #1d439b;
    border-style: solid;
    border-width: 1px;
    font-size: 20px;
}
.searchbar {
  position: relative;
  width: 100%;
  height: 70px;
  padding: 20px 20px 0px 20px;
  box-sizing: border-box;
  background: #1c1c1c;
  color: #fff;
 }
  
.searchbar__content {
  position: relative;
  width: 100%;
  height: 30px;
  border-radius: 40px;
  background:#353535;
  color: #fff;
}

.searchbar__input {
  position: relative;
  height: 20px;
  width: 98%;
  padding: 6px 0 0 20px;
  border: 0;
  background: transparent;
  color: rgb(214, 214, 214);
  font-family: 'Abel', sans-serif;
  font-size: 20px;
}

.input:focus {
  outline: none;
}
.titleGrid {
  /* flex-grow: 1; */
}

.titleGrid h1 {
  font-family: 'Abel', sans-serif;
  font-size: 42px;
}

.titleGrid__content {
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(200px, 1fr));
  max-width: 1390px;
  margin: 0 auto;
  margin-top: 30px;
}

.titleGrid__element {
  max-height: auto;
  width: auto;
  margin: 0 auto 20px;
  overflow: hidden;
  background: transparent;
  -webkit-animation: animateGrid 0.5s;
          animation: animateGrid 0.5s;
  background-size: cover;
}

@-webkit-keyframes animateGrid {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@keyframes animateGrid {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@media screen and (min-width: 1500px) {
  .titleGrid__content {
    max-width: none;
  }
}
.animuThumb {
  position: relative;
  height: 200px;
  width: 184px;
  cursor: pointer;
  background-size: 100% 100%;
  transition: all 0.3s;
}

.animuThumb img {
  height: 100%;
  width: 100%;
}

.animuThumb p {
  position: absolute;
  display: inline-block;
  bottom: 0;
  left: 0;
  width: 95%;
  margin: 0;
  padding: 15px 5px 5px;
  font-family: Avenir,lucida grande,tahoma,verdana,arial,sans-serif;
  font-size: 11px;
  color: #fff;
  line-height: 1.2em;
  text-shadow: rgba(0,0,0,.8) 1px 1px 0;
  background: -webkit-linear-gradient(transparent, #000000);
}

.animuThumb:hover {
  opacity: 0.8;
}
.loadButton {
  width: 100%;
  min-height: 30px;
  padding-top: 10px;
  text-align: center;
  background: #282c34;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s;
}

.loadButton p {
  font-size:15px;
  margin: 0;
}

.loadButton:hover {
  opacity: 0.8;
}
