.title {
  position: absolute;
  top: 50%;
  left: 100px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: white;
  font-family: alsscr;
  font-size: 50px;
  pointer-events: none;
}

@media screen and (max-width: 600px) {

  .title {
    top: auto;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  
}

a {
  text-decoration: none;
}

.mainHeader {
  position: relative;
  height: 340px;
  width: 100%;
  cursor: url('/images/cursor.cur') 10 5, auto;
}
  
canvas {
  width: 100%;
  height: 100%;
}
.three-wrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
  /* display: flex;
  justify-content: center; */
}
.scene-wrap { 
  min-width: 450px;
  width: 100%;
  height: 100%;
  margin-left: auto;
  touch-action: none;
}
.animuInfo {
  width: 100%;
  min-height: 600px;
  padding: 40px 20px;
  box-sizing: border-box;
  background-size: cover;
  background-position: center;
  flex-grow: 1;
}

.animuInfo__title {
  margin: 0;
  padding: 0 0 5px 10px;
}

.animuInfo__content {
  display: grid;
  grid-template-areas: 
  'header header'
  'img desc';
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 3fr;
  position: relative;
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  background: #282c34;
  opacity: 0.9;
}

.animuInfo__thumb {
  grid-area: img;
  width: 250px;
  height: 350px;
  margin: 35px 80px;
}

.animuInfo__text {
  grid-area: desc;
  left: 0;
  width: auto;
  padding: 0 40px 0 0;
  color: #FFF;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

.animuInfo__content h1 {
  grid-area: header;
  margin: 0px 40px;
  padding: 15px 9px 0px;
  text-align: left;
  border-color: transparent;
  border-bottom-color: #1d439b;
  border-style: solid;
  border-width: 1px;
  color: #FFF;
  font-family: Verdana,Arial;
  font-size: 48x;
  font-weight: 700;
}

.animuInfo__text h3 {
  margin-top: 30px;
  margin-bottom: 10px;
  border-color: transparent;
  border-bottom-color: #4d5463;
  border-style: solid;
  border-width: 1px;
  font-size: 20px;
}

.animuInfo__text p {
  font-family: 'Abel', sans-serif;
  font-size: 18px;
  line-height: 26px;
}

.animuInfo__rating {
  position: relative;
  display: inline-block;
  width: 250px;
  height: 20px;
  margin: 20px 0px;
}

.animuInfo__score {
  position: absolute;
  margin: 0;
  right: 0px;
  top: -3px;
}

meter {
  width: 170px;
}

@media screen and (max-width: 800px) {

  .animuInfo__content {
    grid-template-areas: 
    'header'
    'img' 
    'desc';
    grid-template-rows: auto auto auto;
    grid-template-columns: auto;
    height: 100%;
  }

  .animuInfo__thumb {
    display: block;
    margin: 30px auto 20px;
    justify-self: center;
  }

  .animuInfo__text {
    padding: 0 40px 0 40px;
  }

}
